.navbar {
  width: 100%;
  height: 50px;
  background-color: #222831;
  display: flex;
  flex-direction: row;
}

.navbar .leftSide {
  flex: 50% 1;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 15px;
}

.navbar .leftSide img {
  width: 70px;
}

.navbar .rightSide {
  flex: 50% 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: right;
  padding-right: 35px;
}

.navbar a {
  color: #EEEEEE;
  text-decoration: none;
  margin: 20px;
}
.navbar a:hover {
  color: #00ADB5;
}

.navbar .rightSide button {
  background-color: #EEEEEE;
  border: none;
  color: #222831;
  cursor: pointer;

}

.navbar .rightSide svg {
  font-size: 40px;
}

.navbar #open {
  padding-left: 0px;
}
.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar #open .hiddenLinks {
  display: inherit;
  margin-left: 30px;
}

.navbar #close .hiddenLinks {
  display: none;
}

.navbar #open a {
  width: 70px;
  margin: 5px;
}
@media only screen and (max-width: 900px) {
  .navbar .rightSide a {
    width: 70px;
  }
  .navbar .leftSide {
    padding-left: 15px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar .rightSide a {
    display: none;
  }

  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 25px;
  }

  .navbar .rightSide button {
    display: inherit;
  }
}

@media only screen and (min-width: 600px) {
  .navbar .rightSide button {
    display: none;
  }

  .hiddenLinks {
    display: none;
  }
}

.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #EEEEEE
}

body {
  padding: 0;
  margin: 0;
}

.frame {
  display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 200px;
    margin: 20px;
    border: 2px solid #404258;
    width: 70%;
    height: 70%;
    margin: 50px auto 10px auto;
    border-radius: 10px;
}
.video{
  width: 100%;
  height: 100%;
  "object-fit": cover;
  overflow: hidden;
}
.recognized_word{
  display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    /* border: 2px solid #404258; */
    width: 70%;
    height: 70%;

    margin: 0 auto 20px auto;
    /* border-radius: 10px; */
    font-family: Arial, Helvetica, sans-serif;
    color: #393E46;
    font-size: 25px;

}

.image_container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.image_container img {
  height: 50%;
  clip: rect(0px, 300px, 150px, 0px);
}

